













































import {
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			isThird: 0
		};
	},
	onLoad(parameter) {
		if (parseInt(parameter.is_third) === 1) {
			this.isThird = 1;
		}
	},
	methods: {
		...mapMutations(['regSetSex']),
		setSex(sex) {
			this.regSetSex(sex);
			uni.navigateTo({
				url: './selectSchool?is_third=' + this.isThird
			});
		}
	}
}
